<template>
  <div class="wrapper">
    <div class="search">
      <div class="iconfont search__back" @click="toBack">&#xe72a;</div>
      <span class="iconfont search_icon">&#xe621;</span>
      <input type="text" placeholder="请输入商品名称搜索" />
    </div>
    <ShopInfo :item="item" :hideBorder="true" v-show="item.imgUrl" />
    <ContentPart :shopName="item.name" />
    <CartPart />
  </div>
</template>
<script>
import { useRoute } from "vue-router";
import { reactive, toRefs } from "@vue/reactivity";
import { get } from "../../utils/request";
import ShopInfo from "../../components/ShopInfo.vue";
import ContentPart from "./ContentPart.vue";
import CartPart from "./CartPart.vue";
import {toBackEffect} from "../../effects/commonEffect"

const useShopInfoEffect = () => {
  const route = useRoute();
  const data = reactive({ item: {} });
  const getItemData = async () => {
    const result = await get(`/api/shop/${route.params.id}`);
    if (result?.errno === 0 && result?.data) {
      data.item = result.data;
    }
  };
  const { item } = toRefs(data);
  return { item, getItemData };
};

export default {
  name: "ShopView",
  components: { ShopInfo, ContentPart, CartPart },
  setup() {
    const { item, getItemData } = useShopInfoEffect();
    getItemData();
    const { toBack } = toBackEffect();
    return { item, toBack };
  },
};
</script>

<style lang="scss" scoped>
.wrapper {
  padding: 0 0.18rem;
}
.search {
  display: flex;
  position: relative;
  height: 0.32rem;
  margin-top: 0.16rem;
  margin-bottom: 0.04rem;
  color: #b7b7b7;
  &__back {
    margin-top: 0.04rem;
    margin-right: 0.16rem;
    font-size: 0.22rem;
    color: #b6b6b6;
  }
  &_icon {
    position: absolute;
    left: 0.5rem;
    top: 0.08rem;
  }
  input {
    flex: 1;
    padding-left: 0.35rem;
    background-color: #f5f5f5;
    border-radius: 0.16rem;
  }
}
</style>