<template>
  <div class="content">
    <div class="category">
      <div
        :class="{
          category__item: true,
          'category__item--active': currentTab === item.tab,
        }"
        v-for="item in categories"
        :key="item.tab"
        @click="
          () => {
            handleTabClick(item.tab);
          }
        "
      >
        {{ item.name }}
      </div>
    </div>
    <div class="product">
      <div class="product__item" v-for="item in list" :key="item._id">
        <img class="product__item__img" :src="item.imgUrl" />
        <div class="product__item__detail">
          <h4 class="product__item__title">{{ item.name }}</h4>
          <p class="product__item__sales">月售 {{ item.sales }} 件</p>
          <p class="product__item__price">
            <span class="product__item__yen"
              ><em>￥</em><i>{{ item.price }}</i></span
            >
            <span class="product__item__origin">￥{{ item.oldPrice }}</span>
          </p>
        </div>
        <div class="product__number">
          <span
            class="product__number__minus"
            @click="
              () => {
                changeCartItem(shopId, item._id, item, -1, shopName);
              }
            "
            >-</span
          >
          <span class="product__number__text">
            {{ getItemQuantity(shopId, item._id) }}
          </span>
          <span
            class="product__number__plus"
            @click="
              () => {
                changeCartItem(shopId, item._id, item, 1, shopName);
              }
            "
            >+</span
          >
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref } from "@vue/reactivity";
import { get } from "@/utils/request";
import { watchEffect } from "@vue/runtime-core";
import { useRoute } from "vue-router";
import { updateQuantityEffect } from "../../effects/updateQuantity";
import { useStore } from "vuex";
// 获取侧边栏
const categories = [
  { name: "全部商品", tab: "all" },
  { name: "秒杀", tab: "seckill" },
  { name: "水果", tab: "fruit" },
];

// 侧边栏切换
const useTabEffect = () => {
  const currentTab = ref(categories[0].tab);
  const handleTabClick = (tab) => {
    currentTab.value = tab;
  };
  return { currentTab, handleTabClick };
};

// 获取商品列表
const useCurrentListEffect = (currentTab, shopId) => {
  const list = ref([]);
  const getItemData = async () => {
    const result = await get(`/api/shop/${shopId}/products`, {
      tab: currentTab.value,
    });
    if (result?.errno === 0 && result?.data?.length) {
      list.value = result.data;
    }
  };

  watchEffect(() => {
    getItemData();
  });
  return { list };
};

const useCartEffect = () => {
  const { cartList, changeItemQuantity } = updateQuantityEffect();
  const store = useStore();
  const changeShopName = (shopId, shopName) => {
    store.commit("changeShopName", { shopId, shopName });
  };
  const changeCartItem = (shopId, productId, item, num, shopName) => {
    changeItemQuantity(shopId, productId, item, num);
    changeShopName(shopId, shopName);
  };
  const getItemQuantity = (shopId, productId) => {
    return cartList?.[shopId]?.productList?.[productId]?.count || 0;
  };
  return { changeCartItem, getItemQuantity };
};
export default {
  name: "ContentPart",
  props: ["shopName"],
  setup() {
    const route = useRoute();
    const shopId = route.params.id;
    const { currentTab, handleTabClick } = useTabEffect();
    const { list } = useCurrentListEffect(currentTab, shopId);
    const { changeCartItem, getItemQuantity } = useCartEffect();
    return {
      categories,
      list,
      currentTab,
      handleTabClick,
      shopId,
      changeCartItem,
      getItemQuantity,
    };
  },
};
</script>

<style lang="scss" scoped>
@import "../../style/_common.scss";
.content {
  display: flex;
  position: absolute;
  left: 0;
  right: 0;
  top: 1.5rem;
  bottom: 0.5rem;
}
.category {
  width: 0.76rem;
  height: 100%;
  background-color: #f5f5f5;
  overflow-y: scroll;
  &__item {
    line-height: 0.4rem;
    font-size: 0.14rem;
    text-align: center;
    &--active {
      background-color: #fff;
    }
  }
}
.product {
  flex: 1;
  overflow-y: scroll;
  &__item {
    position: relative;
    display: flex;
    padding: 0.12rem 0;
    margin: 0 0.16rem;
    border-bottom: 0.01rem solid #f1f1f1;
    &__img {
      width: 0.68rem;
      height: 0.68rem;
      margin-right: 0.16rem;
    }
    &__detail {
      flex: 1;
      overflow: hidden;
    }
    &__title {
      font-size: 0.14rem;
      font-weight: 600;
      @include ellipsis;
    }
    &__sales {
      margin: 0.09rem 0;
      font-size: 0.13rem;
    }
    &__yen {
      color: #e93b3b;
      margin-right: 0.06rem;
      i {
        font-size: 0.16rem;
      }
    }
    &__origin {
      color: #999;
      text-decoration: line-through;
    }
  }
}
.product__number {
  position: absolute;
  right: 0;
  bottom: 0.12rem;
  &__minus,
  &__plus {
    display: inline-block;
    width: 0.2rem;
    height: 0.2rem;

    border-radius: 50%;
    font-size: 0.2rem;
    text-align: center;
  }
  &__minus {
    border: 0.01rem solid #666;
    line-height: 0.16rem;
    color: #666;
  }
  &__plus {
    background: #0091ff;
    line-height: 0.2rem;
    color: #fff;
  }
  &__text {
    display: inline-block;
    width: 0.25rem;
    text-align: center;
    font-size: 0.14rem;
  }
}
</style>
