import { computed } from "vue";
import { useStore } from "vuex"

export const updateQuantityEffect = (shopId) => {
    const store = useStore();
    const cartList = store.state.cartList
    const changeItemQuantity = (shopId, productId, item, num) => {
        store.commit('changeItemQuantity', {
            shopId, productId, item, num
        })
    }

    const productList = computed(() => {
        const productList = cartList[shopId]?.productList || {};
        let notEmptyProductList = {};
        for (let i in productList) {
            const product = productList[i]
            if (product.count > 0) {
                notEmptyProductList[i] = product
            }
        }
        return notEmptyProductList;
    });

    const orderedProductList = computed(() => {
        const productList = cartList[shopId]?.productList || {};
        let ordered = {};
        for (let i in productList) {
            const product = productList[i]
            if (product.check && product.count > 0) {
                ordered[i] = product
            }
        }
        return ordered;
    })

    const shopName = computed(() => {
        return cartList[shopId]?.shopName || ''
    });

    const Calculations = computed(() => {
        const productList = cartList[shopId]?.productList;
        let result = { total: 0, sum: 0, allChecked: true }
        if (productList) {
            for (const key in productList) {
                const product = productList[key];
                result.total += product.count;
                if (product.check) {
                    result.sum += product.price * product.count;
                }
                if (product.count > 0 && !product.check) {
                    result.allChecked = false;
                }
            }
        }
        result.sum = result.sum.toFixed(2)
        return result;
    })

    return { cartList, productList, Calculations, shopName, orderedProductList, changeItemQuantity }
}