<template>
  <div
    class="mask"
    v-if="showCart && Calculations.total > 0"
    @click="handleCartShowChange"
  ></div>
  <div class="cart">
    <div class="product" v-if="showCart">
      <div class="product__header" v-if="Calculations.total > 0">
        <div
          class="product__header__all"
          @click="() => setItemAllChecked(shopId)"
        >
          <span
            class="product__header__icon iconfont"
            v-html="Calculations.allChecked ? '&#xe810;' : '&#xe7ae;'"
          >
          </span>
          全选
        </div>
        <div
          class="product__header__clear"
          @click="() => cleanCartProducts(shopId)"
        >
          清空购物车
        </div>
      </div>
      <div
        class="product__item"
        v-for="(item, index) in productList"
        :key="index"
      >
        <div
          class="product__item__checked iconfont"
          v-html="item.check ? '&#xe810;' : '&#xe7ae;'"
          @click="
            () => {
              changeItemChecked(shopId, item._id);
            }
          "
        />
        <img class="product__item__img" :src="item.imgUrl" />
        <div class="product__item__detail">
          <h4 class="product__item__title">{{ item.name }}</h4>
          <p class="product__item__price">
            <span class="product__item__yen">
              <em>￥</em><i>{{ item.price }}</i>
            </span>
            <span class="product__item__origin">￥{{ item.oldPrice }}</span>
          </p>
        </div>
        <div class="product__number">
          <span
            class="product__number__minus"
            @click="
              () => {
                changeItemQuantity(shopId, item._id, item, -1);
              }
            "
            >-</span
          ><span class="product__number__text">
            {{ item.count || 0 }}
          </span>
          <span
            class="product__number__plus"
            @click="
              () => {
                changeItemQuantity(shopId, item._id, item, 1);
              }
            "
            >+</span
          >
        </div>
      </div>
    </div>
    <div class="check">
      <div class="check__icon">
        <img
          src="http://www.dell-lee.com/imgs/vue3/basket.png"
          class="check__icon__img"
          @click="handleCartShowChange"
        />
        <div class="check__icon__tag" v-if="Calculations.total > 0">
          {{ Calculations.total }}
        </div>
      </div>
      <div class="check__info">
        总计：<span class="check__info__price">￥{{ Calculations.sum }}</span>
      </div>
      <div class="check__btn" v-if="Calculations.sum > 0">
        <router-link :to="{ path: `/order/${shopId}` }"> 去结算 </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import { ref } from "@vue/reactivity";
import { updateQuantityEffect } from "../../effects/updateQuantity";
import { useStore } from "vuex";
import { useRoute } from "vue-router";

const toggleCartEffect = () => {
  const showCart = ref(false);
  const handleCartShowChange = () => {
    showCart.value = !showCart.value;
  };
  return { showCart, handleCartShowChange };
};

const useCartEffect = (shopId) => {
  const { productList, Calculations, changeItemQuantity } =
    updateQuantityEffect(shopId);

  const store = useStore();
  const setItemAllChecked = (shopId) => {
    store.commit("setItemAllChecked", shopId);
  };

  const cleanCartProducts = (shopId) => {
    store.commit("cleanCartProducts", shopId);
  };

  const changeItemChecked = (shopId, productId) => {
    store.commit("changeItemChecked", { shopId, productId });
  };

  return {
    productList,
    Calculations,
    changeItemQuantity,
    setItemAllChecked,
    cleanCartProducts,
    changeItemChecked,
  };
};

export default {
  name: "CartPart",
  setup() {
    const route = useRoute();
    const shopId = route.params.id;
    const { showCart, handleCartShowChange } = toggleCartEffect();
    const {
      productList,
      Calculations,
      changeItemQuantity,
      setItemAllChecked,
      cleanCartProducts,
      changeItemChecked,
    } = useCartEffect(shopId);
    return {
      showCart,
      shopId,
      productList,
      Calculations,
      handleCartShowChange,
      changeItemQuantity,
      setItemAllChecked,
      cleanCartProducts,
      changeItemChecked,
    };
  },
};
</script>

<style lang="scss" scoped>
@import "../../style/_common.scss";
.mask {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
}
.cart {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
}
.check {
  display: flex;
  height: 0.5rem;
  border-top: 0.01rem solid #f1f1f1;
  background-color: #fff;
  &__icon {
    position: relative;
    width: 0.84rem;
    &__img {
      display: block;
      width: 0.26rem;
      height: 0.28rem;
      margin: 0.12rem auto;
    }
    &__tag {
      position: absolute;
      left: 0.45rem;
      top: 0.06rem;
      width: 0.3rem;
      padding: 0.05rem;
      border-radius: 50%;
      background-color: #e93b3b;
      color: #fff;
      font-size: 0.16rem;
      text-align: center;
      transform: scale(0.5);
      transform-origin: center top;
    }
  }
  &__info {
    flex: 1;
    line-height: 0.5rem;
    &__price {
      font-size: 0.18rem;
      color: #e93b3b;
    }
  }
  &__btn {
    width: 0.98rem;
    height: 0.5rem;
    line-height: 0.5rem;
    background-color: #4fb0f9;
    text-align: center;
    font-size: 0.14rem;
    a {
      color: #fff;
    }
  }
}

.product {
  flex: 1;
  overflow-y: scroll;
  background-color: #fff;
  &__header {
    position: relative;
    padding: 0.12rem 0.16rem;
    border-bottom: 0.01rem solid #f1f1f1;
    font-size: 0.14rem;
    color: #333;
    &__icon {
      color: #0091ff;
      font-size: 0.2rem;
      margin-right: 0.16rem;
      vertical-align: middle;
    }
    &__clear {
      position: absolute;
      top: 0.14rem;
      right: 0.16rem;
    }
  }
  &__item {
    position: relative;
    display: flex;
    padding: 0.12rem 0.16rem;
    &__checked {
      margin-top: 0.15rem;
      color: #0091ff;
      font-size: 0.2rem;
    }
    &__img {
      width: 0.46rem;
      height: 0.46rem;
      margin: 0.04rem 0.16rem;
    }
    &__detail {
      flex: 1;
      overflow: hidden;
    }
    &__title {
      font-size: 0.14rem;
      font-weight: 600;
      line-height: 0.2rem;
      margin-bottom: 0.12rem;
      @include ellipsis;
    }
    &__yen {
      color: #e93b3b;
      margin-right: 0.06rem;
      i {
        font-size: 0.16rem;
      }
    }
    &__origin {
      color: #999;
      text-decoration: line-through;
    }
  }
}
.product__number {
  position: absolute;
  right: 0.16rem;
  bottom: 0.26rem;
  &__minus,
  &__plus {
    display: inline-block;
    width: 0.2rem;
    height: 0.2rem;
    border-radius: 50%;
    font-size: 0.2rem;
    text-align: center;
  }
  &__minus {
    border: 0.01rem solid #666;
    color: #666;
    line-height: 0.16rem;
  }
  &__plus {
    background: #0091ff;
    color: #fff;
    line-height: 0.2rem;
  }
  &__text {
    display: inline-block;
    width: 0.3rem;
    text-align: center;
  }
}
</style>
